import './style.css'

function Header() {
  return (
    <div className='position-fixed w-100 ps-4 pe-4 header-position'>
      <nav id='home' className="navbar navbar-expand-sm header-back row justify-content-between p-0 mb-3">
        <div className="container-fluid p-0">
          <a
            className="navbar-brand col-2 col-lg-3 col-xl-4 d-flex justify-content-left align-items-center ms-4 pt-3 pb-3"
            href="javascript:void(0)"
          >
            <div className='logo-style text-white'>AIRRY</div>
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#mynavbar"
            style={{ 'backgroundColor': 'rgb(83 94 118)' }}
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse me-4" id="mynavbar">
            <div className="col-12 menu-direction justify-content-between align-items-center header-menu-font">
              <a href='#home' className="text-white mt-1 mb-1 text-decoration-none">Home</a>
              <a href="#aboutus" className="text-white mt-1 mb-1 text-decoration-none">About Us</a>
              <a href='#services' className="text-white mt-1 mb-1 text-decoration-none">How It Works</a>
              <a className="text-white mt-1 mb-1 text-decoration-none">FAQ</a>
              <a href='#contactus' className="header-menu-button text-white mt-1 mb-1 pe-4 ps-4 pt-2 pb-2 text-decoration-none">Contact Us</a>
            </div>
          </div>
        </div>
      </nav>
    </div>
  )
}

export default Header;
