import './style.css'

interface IPercentCompnent {
  percent: string
  tag: string
}

function PercentComponent({ percent, tag }: IPercentCompnent) {
  return (
    <>
      <div className='percent-component-style pt-2 pb-2 d-flex align-items-center'>
        <div className='green-round me-3'></div>
        <div className='percent-component-percent text-white me-3'>{percent}</div>
        <div className='percent-component-tag text-white'>{tag}</div>
      </div>
    </>
  )
}

export default PercentComponent;
