import './style.css';
import { Image } from 'react-bootstrap';

function ContactUs() {
  return (
    <>
      <div className='position-relative mt-5 mb-5' id='contactus'>
        <Image src='./assets/contact.png' className='w-100 contactus-bg' />
        <div className='contactus-content'>
            <div className='contactus-title text-white'>Contact Us</div>
        </div>
        <div className='position-absolute w-100 h-100 top-0 left-0 d-flex justify-content-center align-items-center'>
          <input className='contactus-input' placeholder='for Inquiries or Feedback' />
          <button className='contactus-button pt-2 pb-2 ps-4 pe-4'>Message to Us</button>
        </div>
      </div>
    </>
  )
}

export default ContactUs;
