"use client";

import React, {useEffect, useRef, useState} from "react";
import 'bootstrap/dist/css/bootstrap.css';
import './style.css';
import "bootstrap/dist/css/bootstrap.min.css";
import aboutus1 from '../../assests/aboutus (1).png';
import aboutus2 from '../../assests/aboutus (2).png';
import aboutus3 from '../../assests/aboutus (3).png';
import aboutus4 from '../../assests/aboutus (4).png';
import aboutus5 from '../../assests/aboutus (5).png';
import aboutus6 from '../../assests/aboutus (6).png';
import aboutus7 from '../../assests/aboutus (7).png';
import aboutus8 from '../../assests/aboutus (8).png';
import aboutus9 from '../../assests/aboutus (9).png';
import aboutus10 from '../../assests/aboutus (10).png';
import aboutus11 from '../../assests/aboutus (11).png';
import aboutus12 from '../../assests/aboutus (12).png';
import aboutus13 from '../../assests/aboutus (13).png';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import {Timeline, Tween} from "react-gsap";

const items = [
  aboutus1,
  aboutus13,
  aboutus3,
  aboutus4,
  aboutus5,
  aboutus6,
  aboutus7,
  aboutus8,
  aboutus9,
  aboutus10,
  aboutus11,
  aboutus12,
  aboutus2,
];


export default function AboutUs () {
  const [nav1, setNav1] = useState<Slider | undefined>(undefined);
  const [nav2, setNav2] = useState<Slider | undefined>(undefined);
  const slider1 = useRef<Slider | null>(null);
  const slider2 = useRef<Slider | null>(null);

  useEffect(() => {
    if(slider1.current)
      setNav1(slider1.current);
    if(slider2.current)
      setNav2(slider2.current);
  }, []);

  const mainSettings = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    fade: true,
    easeOut: true,
  };

  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    easing: 'in',
    centerMode: true,
    centerPadding: '0px',
    className: 'center',
  };

  return (
    <div className='position-relative'>
      <Slider {...mainSettings}  asNavFor={nav2} ref={slider1}>
        {items.map((item, index) => (
          <div key={index} className='position-relative'>
            <img src={item} alt='Villa Background' className='background-style' />
          </div>
        ))}
      </Slider>
      <Timeline
        target={
          <div className='col-12 col-sm-12 col-lg-5 position-absolute bottom-0' style={{ right: 0 }}>
            <Slider {...settings} className='carousel-align' asNavFor={nav1} ref={slider2} >
              {items.map((item, index) =>
                <img src={item} alt='Villa Background' className='small-image-ourline' />
              )}
            </Slider>
          </div>
        }
      >
        <Tween from={{ opacity: 0, y: '60px' }} to={{ opacity: 1, y: 0 }} />
      </Timeline>
    </div>
  )
}
