import './App.css';
import Home from './view/home';
import AboutUs from "./view/aboutus";
import { Route, Routes, BrowserRouter } from "react-router-dom";

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path='/' element={
          <div className='page-background'>
            <Home />
          </div>
        } />
        <Route path='/aboutus' element={<AboutUs />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
