import './style.css'
import { Image } from 'react-bootstrap'

interface IServiceCompnent {
  title: string
  content: string
}

function ServiceComponent({ title, content }: IServiceCompnent) {
  return (
    <>
      <div className='service-component-style ps-4 pt-4 ms-3 me-3'>
        <Image src="./assets/service.png" />
        <div className='service-component-title text-white mt-3'>{title}</div>
        <div className='service-component-content text-white'>{content}</div>
      </div>
    </>
  )
}

export default ServiceComponent
