import './style.css'
import {Tween} from "react-gsap";

function Main() {
  return (
    <div className='main-back-image'>
      <div className="d-flex flex-column align-items-center">
        <Tween from={{ scale: 0 }} to={{ scale: 1 }} >
          <div className="main-text-style mt-5">
            Decentralized IoT Air Purifier System
          </div>
          <div className="main-content-style mt-3">
            The objective of this project is to develop a Decentralized IoT Air
            Purifier System that leverages blockchain technology and IoT
            connectivity to improve indoor air quality, incentivize users to
            participate in the network, and provide transparent and real-time air
            quality data.
          </div>
          <div className="learn-more-button text-white mt-5 mb-5 pt-2 pb-2 ps-5 pe-5">
            Learn More
          </div>
        </Tween>
        <Tween from={{ opacity: 0, y: '100px' }} to={{ opacity: 1, y: '0px' }} >
          <div className="position-relative mt-5 pt-5 w-100">
            <div className='blur-token-back w-100' />
            <div className="gradient-content-position pt-5 pb-5 m-auto">
              <div className="text-center">
                <div className="active-user-number">+ 1,534,523</div>
                <div className="active-user-title">Active Users</div>
              </div>
              <div className="text-center">
                <div className="active-user-number">+ 52,234</div>
                <div className="active-user-title">Minted NFTs</div>
              </div>
              <div className="text-center">
                <div className="active-user-number">$ 0.00452</div>
                <div className="active-user-title">Token Price</div>
              </div>
            </div>
          </div>
        </Tween>
      </div>
    </div>
  )
}

export default Main;
